$color-primary: #3c4048;
$bg-primary: #EAEAEA;
$bg-operator: #B2B2B2;
$bg-equal: #00ABB3;

body {
  background-color: #ddd;
}

.version {
  position: fixed;
  height: 15px;
  width: 15px;
  top: 0;
  right: 0;
  background-color: #00ABB3;
  overflow: hidden;
  transition: all .3s;
  font-size: 14px;
  padding: 5px;
  .inner {
    display: none;
  }

  &:hover {
    height: 30px;
    width: 300px;
    .inner {
      display: block;
    }
  }
}